import Events from '../events/'

export default props => new Promise((resolve, reject) => {
	switch(props.type){
		case "events":
			console.log(props)
			let data = new Events({props})
			data.go().then( data => resolve(data))
			break;
		default:
			reject({status:-1, msg:'error 8912ty3912'})
	}
})

